import { Box, MenuItem, MenuItemProps, styled } from '@material-ui/core';
import React, { forwardRef, RefObject } from 'react';

const StyledMenuItem = styled(MenuItem)({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
});

const StyledTypography = styled(Box)({
  paddingRight: '8px',
  textAlign: 'left',
  fontSize: '12px',
  fontWeight: 500,
});

const FlexBox = styled(Box)({
  display: 'flex',
  flex: 1,
  alignItems: 'center',
});

const IconContainer = styled('span')({
  marginInlineEnd: '8px',
  alignItems: 'center',
  display: 'flex'
})

type IconMenuItemProps = {
  MenuItemProps?: MenuItemProps;
  className?: string;
  disabled?: boolean;
  label?: string;
  leftIcon?: React.ReactNode;
  onClick?: (event: React.MouseEvent<HTMLElement>) => void;
  ref?: RefObject<HTMLLIElement>;
  rightIcon?: React.ReactNode;
  // sx?: SxProps;
};

export const IconMenuItem = forwardRef<HTMLLIElement, IconMenuItemProps>(function IconMenuItem(
  { className, label, leftIcon, rightIcon, ...props },
  ref
) {
  return (
    <StyledMenuItem ref={ref} className={className} {...props}>
      <FlexBox>
        {leftIcon !== null && <IconContainer>{leftIcon}</IconContainer>}
        <StyledTypography>{label}</StyledTypography>
      </FlexBox>
      {rightIcon}
    </StyledMenuItem>
  );
});