import { ChromeGroupColor } from "../Types";

export const createTab = (url: string) => {
  return chrome.tabs.create({ url });
};

export const reloadTab = (id: number) => {
  return chrome.tabs.reload(id);
};

export const duplicateTab = (id: number) => {
  return chrome.tabs.duplicate(id);
};

export const activateTab = (id: number) => {
  return chrome.tabs.update(id, { active: true });
};

export const togglePinTab = (id: number, pinned: boolean) => {
  chrome.tabs.update(id, { pinned });
};

export const toggleMuteTab = (id: number, muted: boolean) => {
  chrome.tabs.update(id, { muted });
};

export const ungroupTab = (id: number) => {
  chrome.tabs.ungroup(id);
};

export const moveToWindow = (tabId: number, windowId: number) => {
  return chrome.tabs.move(tabId, { windowId, index: -1 });
};

// moves the tab to the new window
export const moveToNewWindow = (id: number) => {
  return chrome.windows.create({ tabId: id });
};

export const closeTabs = (ids: number[]) => {
  return chrome.tabs.remove(ids);
};

export const groupTab = (id: number) => {
  return chrome.tabs.group({ tabIds: id });
};

export const updateGroupColor = (id: number, color: ChromeGroupColor) => {
  return chrome.tabGroups.update(id, { color });
};

export const updateGroupTitle = (id: number, title: string) => {
  return chrome.tabGroups.update(id, { title });
};

export const getFaviconUrl = (pageUrl: string) => {
  const url = new URL(chrome.runtime.getURL("/_favicon/"));
  url.searchParams.set("pageUrl", pageUrl);
  url.searchParams.set("size", "32");
  return url.toString();
};
