import { Divider, Menu, MenuItem } from "@material-ui/core";
import { Add, Brightness1, CropFree, HighlightOff, OpenInBrowser, RadioButtonChecked } from "@material-ui/icons";
import { ReactNode, useRef } from "react";
import styled from "styled-components";
import { updateGroupColor, updateGroupTitle } from "../helpers/chrome";
import { tabGroupContextMenuViewedEvent } from "../helpers/gaEvents";
import { TabGroupColor } from "../helpers/getGroupColor";
import { ChromeGroupColor, GroupedTabs } from "../Types";
import { IconMenuItem } from "./IconMenuItem";

type ContextualGroupProps = {
  children: ReactNode;
  groupedTabs: GroupedTabs;
  open: boolean;
  onContextMenu(): void;
  onCloseMenu(): void;
}

const GroupEditMenuItem = styled(MenuItem)`
  display: block !important;

  &:hover {
    background-color: transparent !important;
  }
`;

const GroupColorContainer = styled.div`
  display: flex;
  width: 100%;
  column-gap: 5px;
  margin-top: 10px;
`;

const GroupColor = styled.div`    
`;

const GroupTitleInput = styled.input`
  padding: 10px;
  color: var(--primary-color);
  width: 100%;
  border: none;
  outline: 1px solid #dddedf;
  border-radius: 3px;
  background-color: var(--background-color);

  @media (prefers-color-scheme: dark) {
    outline: 1px solid #3a3a3a;
  }

  &:focus {
    outline: 2px solid #275dc5;

    @media (prefers-color-scheme: dark) {
      outline: 2px solid #729cef;
    }
  }
`;

const ContextualGroup: React.FC<ContextualGroupProps> = ({ children, groupedTabs, onCloseMenu, onContextMenu, open }) => {
  const groupTitleRef = useRef<HTMLInputElement>(null);
  const mainRef = useRef<HTMLInputElement>(null);
  const handleContextMenu = (event: React.MouseEvent) => {
    event.preventDefault();
    onContextMenu();
    tabGroupContextMenuViewedEvent();
  };

  const closeMenu = () => {
    onCloseMenu();
  };

  const handleGroupTitleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    if (groupedTabs.id) {
      updateGroupTitle(groupedTabs.id, event.target.value)
    }
  }

  const handleGroupColorChange = (color: ChromeGroupColor) => {
    if (groupedTabs.id) {
      updateGroupColor(groupedTabs.id, color)
    }
  }

  const handleMouseLeave = () => {
    groupTitleRef.current?.blur();
  }

  const handleNewTab = async () => {
    closeMenu();
    const tab = await chrome.tabs.create({});
    chrome.tabs.group({ tabIds: tab.id, groupId: groupedTabs.id });
  }

  const handleUngroup = async () => {
    closeMenu();
    chrome.tabs.ungroup(groupedTabs.tabs.map(t => t.id!));
  }

  const handleClose = async () => {
    closeMenu();
    groupedTabs.tabs.forEach(t => chrome.tabs.remove(t.id!));
  }

  const handleNewWindow = async () => {
    closeMenu();
    const newWindow = await chrome.windows.create({});
    if (newWindow.id) {
      await chrome.tabGroups.move(groupedTabs.id, { windowId: newWindow.id, index: 1 });
      if (newWindow.tabs?.length) {
        chrome.tabs.remove(newWindow.tabs[0].id!);
      }
    }
  }

  let [top, left] = [0, 0];
  const rect = mainRef.current?.getBoundingClientRect();
  if (open && rect) {
    top = rect.bottom + 5;
    left = rect.left + 30;
  }

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      closeMenu();
    }
  }

  return (
    <>
      <div onContextMenu={handleContextMenu} ref={mainRef}>
        {children}
      </div>
      <Menu
        // allow escaple key to close menu while not selecting a menu item when it matching the first character(breaks input field)
        onKeyDownCapture={e => !['Escape', 'Enter'].includes(e.key) && e.stopPropagation()}
        open={open}
        onClose={closeMenu}
        anchorReference="anchorPosition"
        anchorPosition={{ top, left }}
        MenuListProps={{
          dense: true
        }}
        PaperProps={{ elevation: 10, style: { backgroundColor: "transparent" } }}
      >
        <GroupEditMenuItem disableRipple={true} onMouseLeave={handleMouseLeave}>
          <GroupTitleInput
            value={groupedTabs.group.title}
            placeholder="Name This Group"
            onChange={handleGroupTitleChange}
            ref={groupTitleRef}
            onKeyDown={handleKeyDown}
          />
          <GroupColorContainer>
            {Object.values(TabGroupColor).map((c, idx) => {
              const colorKey = Object.keys(TabGroupColor)[idx] as ChromeGroupColor;
              const checked = groupedTabs.group.color === colorKey;

              return (
                <GroupColor onClick={() => handleGroupColorChange(colorKey)}>
                  {checked ? <RadioButtonChecked htmlColor={c} fontSize='small' /> : <Brightness1 htmlColor={c} fontSize='small' />}
                </GroupColor>
              );
            })}
          </GroupColorContainer>
        </GroupEditMenuItem>
        <Divider />
        <IconMenuItem onClick={handleNewTab} leftIcon={<Add fontSize="inherit" />} label="New Tab in Group" />
        <IconMenuItem onClick={handleUngroup} leftIcon={<CropFree fontSize="inherit" />} label="Ungroup" />
        <IconMenuItem onClick={handleClose} leftIcon={<HighlightOff fontSize="inherit" />} label="Close group" />
        <IconMenuItem onClick={handleNewWindow} leftIcon={<OpenInBrowser fontSize="inherit" />} label="Move Group to New Window" />
      </Menu>
    </>
  );
}

export default ContextualGroup;