import { Divider, Menu, MenuItem } from "@material-ui/core";
import { Add, Brightness1, CropFree, HighlightOff, OpenInBrowser, RadioButtonChecked } from "@material-ui/icons";
import { useRef } from "react";
import styled from "styled-components";
import { updateGroupColor, updateGroupTitle } from "../helpers/chrome";
import { TabGroupColor } from "../helpers/getGroupColor";
import { ChromeGroupColor, ChromeTabGroup, ContextMenuPosition } from "../Types";
import { IconMenuItem } from "./IconMenuItem";

type TabGroupContextMenuProps = {
  group?: ChromeTabGroup | null;
  tabIds?: number[];
  contextMenuPosition: ContextMenuPosition | null;
  onCloseMenu(): void;
}

const GroupEditMenuItem = styled(MenuItem)`
  display: block !important;

  &:hover {
    background-color: transparent !important;
  }
`;

const GroupColorContainer = styled.div`
  display: flex;
  width: 100%;
  column-gap: 5px;
  margin-top: 10px;
`;

const GroupColor = styled.div`    
`;

const GroupTitleInput = styled.input`
  padding: 10px;
  color: var(--primary-color);
  width: 100%;
  border: none;
  outline: 1px solid #dddedf;
  border-radius: 3px;
  background-color: var(--background-color);

  @media (prefers-color-scheme: dark) {
    outline: 1px solid #3a3a3a;
  }

  &:focus {
    outline: 2px solid #275dc5;

    @media (prefers-color-scheme: dark) {
      outline: 2px solid #729cef;
    }
  }
`;

const TabGroupContextMenu: React.FC<TabGroupContextMenuProps> = ({ group, tabIds, contextMenuPosition, onCloseMenu }) => {
  const groupTitleRef = useRef<HTMLInputElement>(null);

  const closeMenu = () => {
    onCloseMenu();
  };

  const handleGroupTitleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    if (group?.id) {
      updateGroupTitle(group?.id, event.target.value)
    }
  }

  const handleGroupColorChange = (color: ChromeGroupColor) => {
    if (group?.id) {
      updateGroupColor(group?.id, color)
    }
  }

  const handleMouseLeave = () => {
    groupTitleRef.current?.blur();
  }

  const handleNewTab = async () => {
    closeMenu();
    const tab = await chrome.tabs.create({});
    chrome.tabs.group({ tabIds: tab.id, groupId: group?.id });
  }

  const handleUngroup = async () => {
    closeMenu();

    if (tabIds) {
      chrome.tabs.ungroup(tabIds);
    }
  }

  const handleClose = async () => {
    closeMenu();
    tabIds?.forEach(id => chrome.tabs.remove(id));
  }

  const handleNewWindow = async () => {
    closeMenu();
    const newWindow = await chrome.windows.create({});
    if (newWindow.id && group) {
      await chrome.tabGroups.move(group.id, { windowId: newWindow.id, index: 1 });
      if (newWindow.tabs?.length) {
        chrome.tabs.remove(newWindow.tabs[0].id!);
      }
    }
  }

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      closeMenu();
    }
  }

  return (
    <Menu
      // disable filtering of menu items when typing in input
      // while keeping escape and enter to close menu
      onKeyDownCapture={e => !['Escape', 'Enter'].includes(e.key) && e.stopPropagation()}
      open={!!group && !!contextMenuPosition && !!tabIds}
      onClose={closeMenu}
      TransitionProps={{
        onEnter: () => groupTitleRef.current?.focus()
      }}
      anchorReference="anchorPosition"
      anchorPosition={
        contextMenuPosition !== null
          ? { top: contextMenuPosition.mouseY, left: contextMenuPosition.mouseX }
          : undefined
      }
      MenuListProps={{
        dense: true
      }}
      PaperProps={{ elevation: 10, style: { backgroundColor: "transparent" } }}
    >
      <GroupEditMenuItem disableRipple={true} onMouseLeave={handleMouseLeave}>
        <GroupTitleInput
          value={group?.title}
          placeholder="Name This Group"
          onChange={handleGroupTitleChange}
          ref={groupTitleRef}
          onKeyDown={handleKeyDown}
        />
        <GroupColorContainer>
          {Object.values(TabGroupColor).map((c, idx) => {
            const colorKey = Object.keys(TabGroupColor)[idx] as ChromeGroupColor;
            const checked = group?.color === colorKey;

            return (
              <GroupColor key={idx} onClick={() => handleGroupColorChange(colorKey)}>
                {checked ? <RadioButtonChecked htmlColor={c} fontSize='small' /> : <Brightness1 htmlColor={c} fontSize='small' />}
              </GroupColor>
            );
          })}
        </GroupColorContainer>
      </GroupEditMenuItem>
      <Divider />
      <IconMenuItem onClick={handleNewTab} leftIcon={<Add fontSize="inherit" />} label="New Tab in Group" />
      <IconMenuItem onClick={handleUngroup} leftIcon={<CropFree fontSize="inherit" />} label="Ungroup" />
      <IconMenuItem onClick={handleClose} leftIcon={<HighlightOff fontSize="inherit" />} label="Close group" />
      <IconMenuItem onClick={handleNewWindow} leftIcon={<OpenInBrowser fontSize="inherit" />} label="Move Group to New Window" />
    </Menu>
  );
}

export default TabGroupContextMenu;