import { useEffect } from 'react'
import './App.css'
import SearchableTabList from './components/SearchableTabList';
import { GrowthBookProvider } from "@growthbook/growthbook-react";
import growthbook from './analytics/growth-book';
import { pageViewEvent } from './helpers/gaEvents';
import Analytics from './analytics/ga-analytics';

const App = () => {
  useEffect(() => {
    pageViewEvent();
    initGrowthBook();
  }, []);

  const initGrowthBook = async () => {
    growthbook.setAttributes({
      id: await Analytics.getOrCreateClientId(),
      browser: navigator.userAgent,
    });
    growthbook.loadFeatures();
  }

  return (
    <GrowthBookProvider growthbook={growthbook}>
      <div className="App">
        <SearchableTabList />
      </div>
    </GrowthBookProvider>
  )
}

export default App;
