import { DragEndEvent, DragStartEvent } from "@dnd-kit/core";
import { useEffect, useMemo, useState } from "react";
import { ChromeTab, ChromeTabGroup, ChromeTabOrGroup, ChromeTabOrGroupedTabs, ChromeWindow, GroupedTabs, Settings, TabList } from "../Types";
import NoTabs from "./NoTabs";
import SearchBar from "./SearchBar";
import TabsList from "./TabsList";
import styled from "styled-components";
import SettingsMenu from "./SettingsMenu";
import { tabClickedEvent, tabDraggedEvent, tabGroupClickedEvent, tabGroupDraggedEvent, compactViewEnabledEvent, searchClickedEvent, tabClosedEvent, pinnedTabsAsIconsDisabledEvent } from '../helpers/gaEvents';
import { initChromeTabListeners, removeChromeTabListeners } from "../helpers/initChromeTabListeners";
import { arrayMove } from "@dnd-kit/sortable";
import getGroupedTabs from "../helpers/getGroupedTabs";
import { activateTab, closeTabs } from "../helpers/chrome";
import * as Sentry from "@sentry/react";
import TabsListVariant from "./TabsListVariant";
import { useFeatureIsOn } from "../helpers/useFeatureIsOn";

const TopContainer = styled.div`
  display: flex;
`;

const MenuContainer = styled.div`
  font-size: 1.2em;
  margin: 4px 8px;
  display: inline-flex;
`;

const filterTabs = (searchText: string, tabList?: TabList) => {
  if (!tabList) return [];

  const searchLowerCase = searchText.toLowerCase();
  return tabList.reduce<TabList>((acc, tabOrGroup) => {
    if ('group' in tabOrGroup) {
      const filteredGroupedTabs = tabOrGroup.filter(searchLowerCase);
      if (filteredGroupedTabs) {
        acc.push(filteredGroupedTabs);
      }
    } else {
      if (tabOrGroup.title?.toLowerCase().includes(searchLowerCase) || tabOrGroup.url?.toLowerCase().includes(searchLowerCase)) {
        acc.push(tabOrGroup)
      }
    }

    return acc;
  }, []);
}

const SearchableTabList = () => {
  const [tabList, setTabList] = useState<TabList | undefined>([]);
  const [flatItems, setFlatItems] = useState<ChromeTabOrGroup[]>([]);
  const [tabs, setTabs] = useState<ChromeTab[] | undefined>([]);
  const [tabGroups, setTabGroups] = useState<ChromeTabGroup[] | undefined>([]);
  const [windows, setWindows] = useState<ChromeWindow[]>([]);
  const [settings, setSettings] = useState<Settings | undefined>(undefined);
  const [activeItem, setActiveItem] = useState<ChromeTabOrGroupedTabs | null>(null);
  const [searchText, setSearchText] = useState<string>('');
  const filteredTabs = useMemo(
    () => filterTabs(searchText, tabList), [tabList, searchText]);

  const initSettings = () => {
    chrome.storage.sync.get('settings', data => {
      setSettings({
        compactViewEnabled: data.settings?.compactViewEnabled ?? false,
        pinnedTabsAsIcons: data.settings?.pinnedTabsAsIcons ?? true
      });
    });
  }

  useEffect(() => {
    initSettings();
    getTabs();
    initChromeEvents();

    return () => {
      cleanChromeListeners();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const chromeListener = () => {
    getTabs();
  }

  const initChromeEvents = () => {
    initChromeTabListeners(chromeListener);
  }

  const cleanChromeListeners = () => {
    removeChromeTabListeners(chromeListener)
  }

  async function getTabs() {
    const tabGroups: ChromeTabGroup[] = await chrome.tabGroups.query({ windowId: chrome.windows.WINDOW_ID_CURRENT });
    const tabs: ChromeTab[] = await chrome.tabs.query({ currentWindow: true });
    const windows = await chrome.windows.getAll({ populate: true, windowTypes: ["normal"] });

    if (tabs.length) {
      tabs[tabs.length - 1].isLastTab = true;
    } else {
      Sentry.captureMessage('Fetched empty list of tabs', {
        extra: {
          tabs: tabs.length,
          tabGroups: tabGroups.length,
          windows: windows.length,
        }
      });
    }

    setTabs(tabs);
    setTabGroups(tabGroups);
    const groupedTabs = getGroupedTabs(tabs, tabGroups);
    setTabList(groupedTabs);
    setWindows(windows);

    setFlatItems(groupedTabs.reduce<ChromeTabOrGroup[]>((acc, item) => {
      if (item instanceof GroupedTabs) {
        acc.push(item.group);
        acc.push(...item.tabs);
      } else {
        acc.push(item);
      }

      return acc;
    }, []));
  }

  const handleSearchClick = () => {
    searchClickedEvent();
  }

  const handleTabClick = (id?: number) => {
    if (id) {
      activateTab(id);
      tabClickedEvent();
    }
  }

  const handleTabClose = (id?: number) => {
    if (id) {
      chrome.tabs.remove(id);
      tabClosedEvent();
    }
  }

  const handleDragCancel = () => {
    setActiveItem(null);
  };

  const handleTabMove = async (event: DragEndEvent) => {
    setActiveItem(null);
    const { active, over } = event;
    if (active.id === over?.id || !over) return;

    const oldItem = flatItems.find(item => item.id === active.id);
    const newItem = flatItems.find(item => item.id === over.id);

    if (!oldItem || !newItem) {
      return;
    }

    const oldFlatIndex = flatItems.findIndex(item => item.id === active.id);
    const newFlatIndex = flatItems.findIndex(item => item.id === over.id);

    const fromGroup = 'color' in oldItem;
    const toGroup = 'color' in newItem;
    if (fromGroup) {
      const groupedTabsIndex = tabList?.findIndex(i => i.id === oldItem.id);
      if (groupedTabsIndex === undefined) {
        return;
      }

      const tabIndex = tabs?.findIndex(t => {
        if (toGroup) { // this will always be when moving a group backward behind another group, since the other case for toGroup would be moving a group forward after the group header, which is not possible as we can't have nested groups
          // first tab with matching groupId should be the beginning of the group
          return t.groupId === newItem.id;
        }

        return t.id === newItem.id;
      });

      let toIndex = tabIndex;
      if (newFlatIndex > oldFlatIndex) {
        toIndex = tabIndex! - ((tabList?.[groupedTabsIndex] as GroupedTabs).tabs.length - 1);
      }

      const idx = tabList?.findIndex(item => item.id === newItem.id);
      if (idx !== undefined) {
        const sortedList = arrayMove(tabList!, groupedTabsIndex, idx);
        setTabList(sortedList);
      }

      chrome.tabGroups.move(oldItem.id, { index: toIndex! });
    } else if (toGroup && oldItem.groupId === newItem.id) {// upgrouping tab by placing tab right above group
      const groupedTabs = tabList?.find(i => i.id === newItem.id) as GroupedTabs;
      const tabCount = groupedTabs.tabs.length;
      await chrome.tabs.ungroup(oldItem.id!);

      if (tabCount > 1) {
        const firstTabInGroup = groupedTabs.tabs[0];
        chrome.tabs.move(oldItem.id!, { index: firstTabInGroup.index });
      }
    } else if ((oldFlatIndex < newFlatIndex && toGroup) || (oldFlatIndex > newFlatIndex && !toGroup && newItem.groupId !== -1 && tabs?.find(t => t.groupId === newItem.groupId)?.id === newItem.id && oldItem.groupId !== newItem.groupId)) {
      const groupId = (toGroup && newItem.id) || (!toGroup && newItem.groupId);

      if (!groupId) {
        return;
      }

      // this will move it to a random position
      await chrome.tabs.group({ groupId, tabIds: oldItem.id });
      // move it to correct position
      const firstTabInGroup = tabs?.find(t => t.groupId === groupId);
      let index = firstTabInGroup?.index;
      if (index === undefined) {
        return;
      }

      if (oldFlatIndex < newFlatIndex) {
        index--;
      }

      if (oldItem.id) {
        await chrome.tabs.move(oldItem.id, { index });
      }

      // if moving the last tab outside group, we need to ungroup it, API doesn't do this for us
      // if ((toGroup || newItem.groupId !== -1)) {
      //   chrome.tabs.ungroup(oldItem.id!);
      // }
    } else {
      // normal tab move
      const oldIndex = tabs?.findIndex(t => active.id === t.id);
      const newIndex = tabs?.findIndex(t => {
        if (toGroup) {
          return t.groupId === newItem.id;
        }

        return over.id === t.id;
      });
      if (oldIndex !== undefined && newIndex !== undefined) {
        const sortedTabs = arrayMove(tabs!, oldIndex, newIndex);
        const groupedTabs = getGroupedTabs(sortedTabs, tabGroups ?? []);
        setTabList(groupedTabs);
        chrome.tabs.move(active.id as number, { index: newIndex });
      }
    }

    if (fromGroup) {
      tabGroupDraggedEvent();
    } else {
      tabDraggedEvent();
    }
  };

  const handleSettingsChange = async (newSettings: Settings) => {
    const compactViewEnabled = !settings?.compactViewEnabled && newSettings.compactViewEnabled;
    const pinnedTabsAsIconsDisabled = settings?.pinnedTabsAsIcons && !newSettings.compactViewEnabled;

    setSettings(newSettings);
    chrome.storage.sync.set({
      settings: newSettings,
    });

    if (compactViewEnabled) {
      compactViewEnabledEvent();
    }

    if (pinnedTabsAsIconsDisabled) {
      pinnedTabsAsIconsDisabledEvent();
    }
  };

  const handleNewTab = () => {
    chrome.tabs.create({});
  }

  const handleGroupCollapse = (group: ChromeTabGroup) => {
    chrome.tabGroups.update(group.id, { collapsed: !group.collapsed });
    tabGroupClickedEvent();
  }

  const handleGroupNewTab = async (groupedTabs: GroupedTabs) => {
    const lastGroupTab = groupedTabs.tabs[groupedTabs.tabs.length - 1];
    const newTab = await chrome.tabs.create({ index: lastGroupTab.index + 1 });

    if (newTab) {
      chrome.tabs.group({ groupId: groupedTabs.group.id, tabIds: newTab.id });
    }
  }

  const handleCloseOtherTabs = (tab: ChromeTab) => {
    const tabIds = tabs?.reduce<number[]>((acc, t) => {
      if (!t.pinned && t.id !== tab.id && t.id !== undefined) {
        acc.push(t.id);
      }

      return acc;
    }, []);

    if (tabIds?.length) {
      closeTabs(tabIds);
    }
  }

  const handleCloseBelowTab = (tab: ChromeTab) => {
    const tabIds = tabs?.reduce<number[]>((acc, t) => {
      if (!t.pinned && t.index > tab.index && t.id !== undefined) {
        acc.push(t.id);
      }

      return acc;
    }, []);

    if (tabIds?.length) {
      closeTabs(tabIds);
    }
  }

  const handleDragStart = ({ active }: DragStartEvent) => {
    if (!active) {
      return;
    }

    const item = tabList?.find(t => t.id === active.id);

    if (item) {
      setActiveItem(item);
    }
  }

  const isContextMenuFixEnabled = useFeatureIsOn('context_menu_memory_fix');

  if (!settings) {
    return null;
  }


  return (
    <>
      <TopContainer>
        <SearchBar
          text={searchText}
          onChange={setSearchText}
          onClick={handleSearchClick}
        />
        <MenuContainer>
          <SettingsMenu
            settings={settings}
            onSettingsChange={handleSettingsChange}
          />
        </MenuContainer>
      </TopContainer>
      {filteredTabs.length ?
        (isContextMenuFixEnabled ?
          <TabsListVariant
            tabList={filteredTabs}
            tabs={tabs ?? []}
            onClick={handleTabClick}
            onClose={handleTabClose}
            onTabMove={handleTabMove}
            onNewTab={handleNewTab}
            onGroupCollapseChange={handleGroupCollapse}
            onGroupNewTab={handleGroupNewTab}
            settings={settings}
            onDragStart={handleDragStart}
            onDragCancel={handleDragCancel}
            activeItem={activeItem}
            windows={windows}
            onCloseBelowTab={handleCloseBelowTab}
            onCloseOtherTabs={handleCloseOtherTabs}
          />
          :
          <TabsList
            tabList={filteredTabs}
            tabs={tabs ?? []}
            onClick={handleTabClick}
            onClose={handleTabClose}
            onTabMove={handleTabMove}
            onNewTab={handleNewTab}
            onGroupCollapseChange={handleGroupCollapse}
            onGroupNewTab={handleGroupNewTab}
            settings={settings}
            onDragStart={handleDragStart}
            onDragCancel={handleDragCancel}
            activeItem={activeItem}
            windows={windows}
            onCloseBelowTab={handleCloseBelowTab}
            onCloseOtherTabs={handleCloseOtherTabs}
          />)
        : <NoTabs />}
    </>
  );
}

export default SearchableTabList;