import { ChromeTab, ChromeTabGroup, GroupedTabs, TabList } from "../Types";

const getGroupedTabs = (tabs: ChromeTab[], groups: ChromeTabGroup[]) => {
  const groupsMap: Record<string, number> = {};

  return tabs.reduce<TabList>((acc, tab) => {
    // is tab grouped?
    if (tab.groupId != -1) {
      let idx;
      // check if group is saved already
      if (!(tab.groupId in groupsMap)) {
        const group = groups.find((g) => g.id === tab.groupId);

        // if group is not found, consider it ungrouped
        if (!group) {
          acc.push(tab);
          return acc;
        }

        idx = acc.length;
        // save the index of the group
        groupsMap[tab.groupId] = idx;
        acc.push(new GroupedTabs({ group: group!, tabs: [] }));
      } else {
        // group already exist, we just need to find where it is
        idx = groupsMap[tab.groupId];
      }

      // add the tab under current group
      (acc[idx] as GroupedTabs).tabs.push(tab);
    } else {
      // ungrouped tabs can be added immediately
      acc.push(tab);
    }

    return acc;
  }, []);
};

export default getGroupedTabs;
