import { Divider, Menu, MenuItem } from "@material-ui/core";
import { duplicateTab, moveToNewWindow, moveToWindow, reloadTab, toggleMuteTab, togglePinTab, ungroupTab } from "../helpers/chrome";
import { ChromeTab, ChromeWindow, ContextMenuPosition, GroupedTabs } from "../Types";
import { NestedMenuItem } from "./NestedMenuItem";

type TabContextMenuProps = {
  tab?: ChromeTab | null;
  onCloseOtherTabs(tab: ChromeTab): void;
  onCloseBelowTab(tab: ChromeTab): void;
  onNewGroupCreated(groupId: number): void;
  windows: ChromeWindow[];
  groupedTabs: GroupedTabs[];
  contextMenuPosition?: ContextMenuPosition | null;
  closeMenu: () => void;
};

const TabContextMenu: React.FC<TabContextMenuProps> = ({ tab, contextMenuPosition, closeMenu, onCloseOtherTabs, onCloseBelowTab, windows, onNewGroupCreated, groupedTabs }) => {

  if (!tab || !contextMenuPosition) {
    return null;
  }

  const handleReload = () => {
    closeMenu();

    if (tab?.id) {
      reloadTab(tab.id);
    }
  };

  const handleDuplicate = () => {
    closeMenu();

    if (tab?.id) {
      duplicateTab(tab.id);
    }
  };

  const handlePin = () => {
    closeMenu();

    if (tab?.id) {
      togglePinTab(tab.id, !tab.pinned);
    }
  };

  const handleMute = () => {
    closeMenu();

    if (tab?.id) {
      toggleMuteTab(tab.id, !tab.mutedInfo?.muted);
    }
  };

  const handleNewTabBelow = () => {
    closeMenu();

    chrome.tabs.create({ index: tab.index + 1 });
  };

  const handleUngroup = () => {
    closeMenu();

    if (tab?.id) {
      ungroupTab(tab.id);
    }
  };

  const handleMoveToWindow = (window: ChromeWindow) => {
    closeMenu();

    if (tab?.id && window.id) {
      moveToWindow(tab.id, window.id);
    }
  };

  const handleMoveToNewWindow = () => {
    closeMenu();

    if (tab?.id) {
      moveToNewWindow(tab.id);
    }
  };

  const handleCloseOthers = () => {
    closeMenu();

    onCloseOtherTabs(tab);
  };

  const handleCloseBelow = () => {
    closeMenu();

    onCloseBelowTab(tab);
  };

  const handleMoveToGroup = async (groupId?: number) => {
    if (tab.id === undefined) {
      return;
    }

    // move to new/existing group (if existing it will automatically ungroup it from old)
    const newGroupId = await chrome.tabs.group({ tabIds: tab.id!, groupId });

    if (groupId === undefined) {
      onNewGroupCreated(newGroupId);
    }

    closeMenu();
  }

  const getWindowTitle = (window: ChromeWindow) => {
    if (!window?.tabs || !window.tabs?.length) {
      return "";
    }

    if (window.tabs?.length === 1) {
      return window.tabs[0].title;
    }

    const activeTab = window.tabs?.find((t) => t.active);
    const concatTabTitle = activeTab?.title;//?.slice(0, 15);
    const tabCount = window.tabs?.length - 1;
    const otherText = tabCount === 1 ? "Other Tab" : "Other Tabs";

    return [concatTabTitle, `${tabCount} ${otherText}`].join(" and ");
  };

  const getGroupTitle = (groupedTabs: GroupedTabs) => {
    if (groupedTabs.group.title) {
      return groupedTabs.group.title;
    }

    if (groupedTabs.tabs?.length === 1) {
      return groupedTabs.tabs[0].title;
    }

    const firstTab = groupedTabs.tabs[0];
    const concatTabTitle = firstTab?.title;
    const tabCount = groupedTabs.tabs?.length - 1;
    const otherText = tabCount === 1 ? "Other Tab" : "Other Tabs";

    return [concatTabTitle, `${tabCount} ${otherText}`].join(" and ");
  }

  const otherGroups = tab.groupId !== -1 ? groupedTabs?.filter(g => tab.groupId !== g.id) : groupedTabs;

  return (
    <Menu
      open={contextMenuPosition !== null}
      onClose={closeMenu}
      anchorReference="anchorPosition"
      anchorPosition={
        contextMenuPosition !== null
          ? { top: contextMenuPosition.mouseY, left: contextMenuPosition.mouseX }
          : undefined
      }
      MenuListProps={{
        dense: true,
      }}
      PaperProps={{
        elevation: 10,
        style: { backgroundColor: "transparent" },
      }}
    >
      <MenuItem onClick={handleNewTabBelow}>New Tab Below</MenuItem>
      {!!otherGroups.length && < NestedMenuItem
        label="Add Tab to Group"
        parentMenuOpen={contextMenuPosition !== null}
      >
        <>
          <MenuItem onClick={() => handleMoveToGroup()}>New Group</MenuItem>
          <Divider />
          {otherGroups.map(g => (
            <MenuItem onClick={() => handleMoveToGroup(g.id)}>
              {getGroupTitle(g)}
            </MenuItem>
          ))}
        </>
      </NestedMenuItem>}
      {!otherGroups.length && <MenuItem onClick={() => handleMoveToGroup()}>Add Tab to New Group</MenuItem>
      }
      {tab.groupId !== -1 && (
        <MenuItem onClick={handleUngroup}>Remove From Group</MenuItem>
      )}
      {windows.length > 1 && (
        <NestedMenuItem
          label="Move Tab to Another Window"
          parentMenuOpen={contextMenuPosition !== null}
        >
          <>
            <MenuItem onClick={handleMoveToNewWindow}>New Window</MenuItem>
            <Divider />
            {windows &&
              windows
                .filter((w) => w.id !== tab.windowId)
                .map((w) => (
                  <MenuItem onClick={() => handleMoveToWindow(w)}>
                    {getWindowTitle(w)}
                  </MenuItem>
                ))}
          </>
        </NestedMenuItem>
      )}
      {windows.length === 1 && (
        <MenuItem onClick={handleMoveToNewWindow}>
          Move Tab to New Window
        </MenuItem>
      )}
      <Divider />
      <MenuItem onClick={handleReload}>Reload</MenuItem>
      <MenuItem onClick={handleDuplicate}>Duplicate</MenuItem>
      <MenuItem onClick={handlePin}>{tab.pinned ? "Unpin" : "Pin"}</MenuItem>
      <MenuItem onClick={handleMute}>
        {tab.mutedInfo?.muted ? "Unmute Site" : "Mute Site"}
      </MenuItem>
      <Divider />
      <MenuItem onClick={handlePin}>Close</MenuItem>
      <MenuItem onClick={handleCloseOthers}>Close Other Tabs</MenuItem>
      <MenuItem disabled={tab.isLastTab} onClick={handleCloseBelow}>
        Close Tabs Below
      </MenuItem>
    </Menu>
  );
};

export default TabContextMenu;
