import { MenuItem, Tooltip } from "@material-ui/core";
import CheckIcon from "@material-ui/icons/Check";
import styled from "styled-components";

type SettingsMenuItemProps = {
  title: string;
  selected?: boolean;
  onClick(): void;
  hint?: string;
}

const IconContainer = styled.div`
  display: inline-flex;
  align-items: center;
  width: 15px;
  height: 15px;
  margin-inline-end: 15px;
`;

const Title = styled.div`
  font-size: 13px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 2px;
`;

const SettingsMenuItem: React.FC<SettingsMenuItemProps> = ({ title, hint, selected, onClick }) => {
  return (
    <Tooltip title={hint ?? ''}>
      <MenuItem onClick={onClick}>
        <IconContainer>{selected && <CheckIcon fontSize="small" />}</IconContainer>
        <Title>{title}</Title>
      </MenuItem>
    </Tooltip>
  )
}

export default SettingsMenuItem;
