import { ChromeGroupColor } from "../Types";

export const TabGroupColor: Record<ChromeGroupColor, string> = {
  grey: "var(--chrome-group-color-grey)",
  blue: "var(--chrome-group-color-blue)",
  red: "var(--chrome-group-color-red)",
  yellow: "var(--chrome-group-color-yellow)",
  green: "var(--chrome-group-color-green)",
  pink: "var(--chrome-group-color-pink)",
  purple: "var(--chrome-group-color-purple)",
  cyan: "var(--chrome-group-color-cyan)",
  orange: "var(--chrome-group-color-orange)",
};

export const getGroupColor = (color: ChromeGroupColor) => {
  return TabGroupColor[color];
};

export const getTextGroupColor = (
  color: ChromeGroupColor,
  isDarkMode: boolean
) => {
  const darkTextForLightMode = ["yellow", "orange"];
  if (!isDarkMode && darkTextForLightMode.includes(color)) {
    return "var(--chrome-group-text-color-secondary)";
  }

  return "var(--chrome-group-text-color)";
};
