import styled from 'styled-components';
import SearchIcon from '@material-ui/icons/SearchRounded';

type SearchProps = {
  text?: string;
  onChange: (text: string) => void;
  onClick: () => void;
};

const SearchIconContainer = styled.div`
  font-size: 1.2em;
  margin-inline-end: 10px;
  display: inline-flex;
`;

const SearchBarContainer = styled.div`
  padding: 10px;
  display: flex;
  width: 100%;
  align-items: center;

  input {
    border: none;
    background: 0 0;
    padding: 0;
    caret-color: rgb(25, 103, 210);
    color: var(--primary-color);
    width: 100%;
  }

  input:focus {
    outline: none;
  }
`;

const SearchBar: React.FC<SearchProps> = ({ text, onChange, onClick }) => (
  <SearchBarContainer onClick={onClick}>
    <SearchIconContainer>
      <SearchIcon htmlColor='var(--icon-color)' fontSize='inherit' />
    </SearchIconContainer>
    <input
      type='text'
      placeholder="Search"
      value={text}
      onChange={e => onChange(e.target.value)}
    />
  </SearchBarContainer>
);

export default SearchBar;