export type ChromeTab = chrome.tabs.Tab & { isLastTab?: boolean };
export type ChromeTabGroup = chrome.tabGroups.TabGroup;
export type ChromeTabOrGroupedTabs = GroupedTabs | ChromeTab;
export type TabList = Array<ChromeTabOrGroupedTabs>;
export type ChromeTabOrGroup = ChromeTabGroup | ChromeTab;
export type ChromeWindow = chrome.windows.Window;
export type Settings = {
  compactViewEnabled: boolean;
  pinnedTabsAsIcons: boolean;
};
export type ChromeGroupColor = chrome.tabGroups.ColorEnum;

interface GroupedTabsProps {
  group: ChromeTabGroup;
  tabs: ChromeTab[];
}

export interface ContextMenuPosition {
  mouseX: number;
  mouseY: number;
}

export class GroupedTabs {
  group: ChromeTabGroup;
  tabs: ChromeTab[];
  id: number;

  constructor(props: GroupedTabsProps) {
    this.tabs = props.tabs;
    this.group = props.group;
    this.id = props.group.id;
  }

  filter(text: string): GroupedTabs | null {
    if (!text) {
      return this;
    }

    let tabs: ChromeTab[] = [];
    const matchesGroup = this.group.title?.toLowerCase().includes(text);

    if (matchesGroup) {
      tabs = this.tabs;
    } else {
      tabs = this.tabs.filter(
        (t) =>
          t.title?.toLowerCase().includes(text) ||
          t.url?.toLowerCase().includes(text)
      );
    }

    if (!tabs.length) {
      return null;
    }

    return new GroupedTabs({ group: this.group, tabs });
  }
}
