import { Divider, IconButton, Menu } from "@material-ui/core";
import { useState } from "react";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import SettingsMenuItem from "./SettingsMenuItem";
import { Settings } from "../Types";
import { BUY_COFFEE_URL, CHROME_EXTENSION_SHORTCUT_URL, CHROME_SIDE_PANEL_SETTINGS_URL, GITHUB_ISSUES_URL, RATE_EXTENTION_URL } from "../Constants";
import { createTab } from "../helpers/chrome";
import { buyCoffeeEvent, changePositionClickedEvent, changeShortcutClickedEvent, settingsClickedEvent, supportClickedEvent } from "../helpers/gaEvents";

type SettingsMenuOptions = {
  settings: Settings;
  onSettingsChange(settings: Settings): void;
}

const SettingsMenu: React.FC<SettingsMenuOptions> = ({ settings, onSettingsChange }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
    settingsClickedEvent();
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleCompactViewChange = () => {
    onSettingsChange({
      ...settings,
      compactViewEnabled: !settings.compactViewEnabled,
    })
  }

  const handlePinnedTabsAsIconsChange = () => {
    onSettingsChange({
      ...settings,
      pinnedTabsAsIcons: !settings.pinnedTabsAsIcons,
    })
  }

  const handleSupport = () => {
    window.open(GITHUB_ISSUES_URL, '_blank');
    supportClickedEvent();
  }

  const handleRateExtension = () => {
    window.open(RATE_EXTENTION_URL, '_blank');
  }

  const handleChangePosition = () => {
    createTab(CHROME_SIDE_PANEL_SETTINGS_URL);
    changePositionClickedEvent();
  }

  const handleChangeShortcut = () => {
    createTab(CHROME_EXTENSION_SHORTCUT_URL);
    changeShortcutClickedEvent();
  }

  const handleBuyCoffee = () => {
    window.open(BUY_COFFEE_URL, '_blank');
    buyCoffeeEvent();
  }

  return (
    <>
      <IconButton size="small" onClick={handleClick}>
        <MoreVertIcon fontSize="small" htmlColor='var(--icon-color)' />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        MenuListProps={{
          dense: true
        }}
        PaperProps={{ elevation: 10, style: { backgroundColor: "transparent" } }}
      >
        <SettingsMenuItem
          title="Compact view"
          selected={settings.compactViewEnabled}
          onClick={handleCompactViewChange}
        />
        <SettingsMenuItem
          title="Pinned tabs as icons"
          selected={settings.pinnedTabsAsIcons}
          onClick={handlePinnedTabsAsIconsChange}
        />
        <SettingsMenuItem
          title="Change position"
          onClick={handleChangePosition}
        />
        <SettingsMenuItem
          title="Change shortcut"
          onClick={handleChangeShortcut}
        />
        <Divider />
        <SettingsMenuItem
          title="Rate extension"
          onClick={handleRateExtension}
        />
        <SettingsMenuItem
          title="Submit bug/feature"
          onClick={handleSupport}
        />
        <SettingsMenuItem
          title="Buy me a coffee"
          onClick={handleBuyCoffee}
        />
      </Menu>
    </>
  );
};

export default SettingsMenu;